import React, { Component } from 'react'
import { connect } from "react-redux"
import { Card } from 'antd';
import { Input, Button, Popup, Label, List, Icon } from 'semantic-ui-react'
import Head from './components/headAdmin';
import ChainList from "../../../components/ChainSelect"
import ChainSelectIDto from '../../../components/ChainSelect/chainSelectIDto'
import web3 from '../../../ABI/web3';
import CreactList from './components/creactList';
import erc20abi from '../../../ABI/ERC20.abi.json';
import Loading from '../../../components/loadingReport';
import bridgeAbi from '../../../ABI/Bridge.abi.json';
import { Toast } from 'antd-mobile'
import { getChainInfo, isApprove, SelectWeb3 } from '../../../components/global/functions';
import Fun from "../../../components/global/function";
const BigNumber = require('bignumber.js');

export class bridgeCreate extends Component {
  constructor(props) {
    super(props)

    this.state = {
      onceBridgeCode: '',
      currentCoin: '456',
      toHaveToken: 'yes',
      tokenOrAdr: '',
      selectWeb3: '',
      tokenName: 'N/A',
      tokenSymbol: 'N/A',
      tokenTotalSupply: 'N/A',
      tokenDecimals: '',
      ChainID: '',
      bridgeAdr: '',
      LGadr: '',
      LGApproveDisplay: 'none', //LG
      bridgeContract: '',
      minValue: '',
      curChainTokenAdr: '',
      selectChainID: '',
      fee: 0,
      admin: '',
      loading: false,
      UserBalanceOk: false
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.ChainID !== this.props.ChainID
      || nextProps.UserAdr !== this.props.UserAdr) {
      this.setState({ loading: true })
      await this.first()
      this.setState({ loading: false })
    }
  }

  async componentDidMount() {
    this.setState({ loading: true })
    await this.first()
    await this.admin()
    this.setState({ loading: false })
  }

  admin = async () => {
    let admin = await this.state.bridgeContract.methods.admin().call()
    this.setState({ admin })
  }

  Creatfee = async () => {
    let result = await this.state.bridgeContract.methods.Creatfee().call()
    return result
  }

  first = async () => {
    //
    await getChainInfo(window.ethereum.chainId).then((result) => {
      this.setState({
        ChainID: result.ChainID,
        bridgeAdr: result.ChainAdr.bridge,
        LGadr: result.ChainAdr.LG,
      })
    })
    //LG
    await isApprove(this.state.LGadr, this.state.bridgeAdr).then((result) => {
      if (result === 'no') {
        this.setState({ LGApproveDisplay: 'block' })
      } else {
        this.setState({ LGApproveDisplay: 'none' })
      }
    })
    const bridgeContract = await new web3.eth.Contract(bridgeAbi.abi, this.state.bridgeAdr)
    await this.setState({ bridgeContract })
    let creatFee = await this.Creatfee()
    let fee = Number(await this.feeLG(creatFee))
    this.setState({ fee })
  }

  Approve = async (adr) => {
    await this.Fun.Approve(adr, this.state.bridgeAdr)
    this.componentDidMount();
  }

  toHaveToken = (yesOrNo) => {
    this.setState({ tokenName: 'N/A', tokenOrAdr: '', curChainTokenAdr: '', toHaveToken: yesOrNo })
  }

  chainSelectIDto = async (chainId) => {
    const selectWeb3 = await SelectWeb3(chainId)
    this.setState({ selectWeb3, selectChainID: chainId })
    if (this.state.tokenOrAdr !== '') {
      this.getTokenInfo(this.state.tokenOrAdr)
    }
  }

  getTokenInfo = async (adr) => {
    await this.setState({ tokenName: 'N/A', tokenOrAdr: adr })
    if (this.state.ChainID === this.state.selectChainID) {
      await this.setState({ curChainTokenAdr: this.state.tokenOrAdr })
    } else {
      this.setState({ curChainTokenAdr: '' })
    }
    try {
      const ERC20 = await new this.state.selectWeb3.eth.Contract(erc20abi.abi, this.state.tokenOrAdr)
      const tokenName = await ERC20.methods.name().call()
      const tokenSymbol = await ERC20.methods.symbol().call()
      let _totalSupply = await ERC20.methods.totalSupply().call()
      const tokenDecimals = await ERC20.methods.decimals().call()
      let total = await new BigNumber(_totalSupply * 10 ** (18 - tokenDecimals)).toFixed()
      const tokenTotalSupply = web3.utils.fromWei(total, "ether")
      let userBalance = await ERC20.methods.balanceOf(window.defaultAccount).call()
      let UserBalance = await new BigNumber(userBalance - _totalSupply * 0.1).toFixed()
      if (Number(UserBalance) >= 0) {
        this.setState({ UserBalanceOk: true })
      } else {
        this.setState({ UserBalanceOk: false })
      }
      this.setState({ tokenName, tokenSymbol, tokenTotalSupply, tokenDecimals, tokenOrAdr: adr })
    } catch (e) {
      if (e) {
        Toast.show({
          position: 'top',
          content: ''
        })
      }
    }
  }

  createMapERC20 = async () => {
    if (Number(this.props.LGbalance) < this.state.fee * 2) {
      return Toast.show({ content: 'LG' })
    }
    if (await this.firstOwner() !== '0x0000000000000000000000000000000000000000') {
      return Toast.show({ content: '' })
    }
    if (this.state.UserBalanceOk === false) {
      return Toast.show({
        content: '10%',
        duration: 5000
      })
    }
    this._createMapERC20()
  }

  _createMapERC20 = async () => {
    await this.state.bridgeContract.methods.createMapERC20(
      this.state.ChainID,
      this.state.tokenOrAdr,
      this.state.tokenName,
      this.state.tokenSymbol,
      this.state.tokenDecimals
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "")
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', "")
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "")
        }
      })
  }

  _addFirstOwner = async () => {
    try {
      await this.state.bridgeContract.methods.addFirstOwner(
        this.state.ChainID,
        this.state.tokenOrAdr,
        this.state.curChainTokenAdr
      ).send({ from: window.defaultAccount })
        .on('transactionHash', (transactionHash) => {
          if (transactionHash.length !== 0) {
            this.loading.loadingOpen('block', 'none', 'none', "")
          }
        })
        .on('receipt', (receipt) => {
          if (receipt.status === true) {
            this.loading.loadingOpen('none', 'block', 'none', "")
          }
        })
        .on('error', (error, receipt) => {
          if (receipt !== undefined && receipt.status === false) {
            this.loading.loadingOpen('none', 'none', 'block', "")
          }
        })
    } catch {
      Toast.show({
        content: ''
      })
    }
  }

  addFirstOwner = async () => {
    if (Number(this.props.LGbalance) < this.state.fee) {
      return Toast.show({ content: 'LG' })
    }
    if (await this.firstOwner() !== '0x0000000000000000000000000000000000000000') {
      return Toast.show({ content: '' })
    }
    if (this.state.UserBalanceOk === false) {
      return Toast.show({
        content: '10%',
        duration: 5000
      })
    }
    this._addFirstOwner()
  }

  AdminAddFirstOwner = async () => {
    if (Number(this.props.LGbalance) < this.state.fee) {
      return Toast.show({ content: 'LG' })
    }
    if (await this.firstOwner() !== '0x0000000000000000000000000000000000000000') {
      return Toast.show({ content: '' })
    }
    try {
      await this.state.bridgeContract.methods.AdminAddFirstOwner(
        this.state.ChainID,
        this.state.tokenOrAdr,
        this.state.curChainTokenAdr
      ).send({ from: window.defaultAccount })
        .on('transactionHash', (transactionHash) => {
          if (transactionHash.length !== 0) {
            this.loading.loadingOpen('block', 'none', 'none', "")
          }
        })
        .on('receipt', (receipt) => {
          if (receipt.status === true) {
            this.loading.loadingOpen('none', 'block', 'none', "")
          }
        })
        .on('error', (error, receipt) => {
          if (receipt !== undefined && receipt.status === false) {
            this.loading.loadingOpen('none', 'none', 'block', "")
          }
        })
    } catch {
      Toast.show({
        content: ''
      })
    }
  }

  firstOwner = async () => {
    try {
      const is = await this.state.bridgeContract.methods.firstOwner(
        this.state.tokenOrAdr).call()
      return is;
    } catch {
      Toast.show({
        content: ''
      })
    }
  }

  feeLG = async (value) => {
    let __result
    await this.state.bridgeContract.methods.feeLG(value).call().then((result) => {
      let _result = parseFloat(web3.utils.fromWei(result, "ether")).toFixed(0)
      __result = _result
    })
    return __result
  }

  render() {
    let state = this.state
    return (
      <div>
        <Head />
        <Card type="inner" size='small' className='bridgeUser' style={{ maxWidth: '500px', color: 'white', margin: '0px auto auto auto' }}>
          <div style={{ textAlign: 'center', margin: '0 auto 5px auto' }}>
            <Button.Group >
              <Button > </Button>
              <Popup
                trigger={<Button loading={state.loading} primary icon><Icon name='circle outline' /></Button>}
                content='LG10%'
                inverted
                position='top center'
                on='click'
              />
              <Button >
                <ChainList />
              </Button>
            </Button.Group>
          </div>
          <Button positive fluid
            style={{ marginBottom: '5px', display: state.LGApproveDisplay }}
            onClick={() => { this.Approve(state.LGadr) }}>LG
          </Button>
          <div>
            <Button.Group >
              <Button positive={state.toHaveToken === 'yes' ? true : false} onClick={() => { this.toHaveToken('yes') }}
              ></Button>
              <Popup
                trigger={<Button.Or text='?' />}
                content=''
                inverted
                on='click'
              />
              <Button positive={state.toHaveToken === 'no' ? true : false} onClick={() => { this.toHaveToken('no') }}></Button>
            </Button.Group>
          </div>
          <Button style={{ marginTop: '5px', width: '100%' }} as='div' labelPosition='left'  >
            <Label pointing='right'>
              2 
            </Label>
            <Button positive>
              <ChainSelectIDto chainSelectIDto={this.chainSelectIDto} />
            </Button>
          </Button>
          <div style={{ clear: 'both' }} />

          <Popup
            trigger={
              <Input style={{ marginTop: '5px' }}
                label='3'
                labelPosition='left '
                fluid
                value={state.tokenOrAdr}
                placeholder=''
                actionPosition='left'
                onChange={event => { this.getTokenInfo(event.target.value) }}
              />
            }
            content=''
            inverted
            on='focus'
          />
          <CreactList name={state.tokenName} symbol={state.tokenSymbol} totalSupply={state.tokenTotalSupply} />
          {/*  */}
          <div style={{ display: state.toHaveToken === 'yes' ? 'block' : 'none' }}>
            <div style={{ display: state.ChainID === state.selectChainID ? 'none' : 'block' }}>
              <Popup
                trigger={
                  <Input style={{ marginTop: '5px' }}
                    label='4'
                    labelPosition='left '
                    fluid
                    placeholder=''
                    actionPosition='right'
                    value={state.curChainTokenAdr}
                    onChange={event => { this.setState({ curChainTokenAdr: event.target.value }) }}
                  />
                }
                content=''
                inverted
                on='focus'
              />
            </div>
            <Button positive fluid style={{ marginTop: '5px' }}
              onClick={() => { this.addFirstOwner() }}
            >
            </Button>
            <div style={{ display: this.props.UserAdr.toLowerCase() === state.admin.toLowerCase() ? 'block' : 'none' }}>
              <Button positive fluid style={{ marginTop: '5px' }}
                onClick={() => { this.AdminAddFirstOwner() }}
              >LG
              </Button>
            </div>
            <div style={{ clear: 'both' }} />
            <List divided inverted relaxed style={{ color: 'white', wordWrap: 'break-word', marginTop: '5px' }}>
              <Popup
                trigger={
                  <List.Item >
                    {state.fee}LG
                  </List.Item>
                }
                content=''
                inverted
                on='focus'
              />
            </List>
          </div>

          {/*  */}
          <div style={{ display: state.toHaveToken === 'no' ? 'block' : 'none' }}>
            <Button positive fluid style={{ marginTop: '5px', marginBottom: '5px' }}
              onClick={() => { this.createMapERC20() }}
            >
            </Button>
            <div style={{ clear: 'both' }} />
            <div>
              <List divided inverted relaxed style={{ color: 'white', wordWrap: 'break-word' }}>
                <List.Item >
                  {state.fee * 2}LG
                </List.Item>
              </List>
            </div>
          </div>

        </Card>
        <Loading ref={loadwindow => { this.loading = loadwindow }} />
        <Fun ref={Fun => { this.Fun = Fun }} />
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(bridgeCreate) 
