import React, { Component } from 'react';
import '../../App.css';
import { Card, Button, Input, Popover } from 'antd';
import { QuestionCircleOutlined } from '@ant-design/icons';
import HangQin from '../../ABI/hangqin';

class Hangqinadd extends Component {
  state = {
    tokenA: '',
    tokenB: '',
    tokenUSD: '',
    tokenABpool: '',
    tokenBUSDpool: '',
    disabledB: '',
    disabledU: '',
    disabledBU: '',
    isU: ''
  };

  async componentDidMount() {

  }

  isHT = async () => {
    this.setState({
      isU: '',
      tokenB: '0x5545153ccfca01fbd7dd11c0b23ba694d9509a6f',
      tokenUSD: '0xa71EdC38d189767582C38A3145b5873052c3e47a',
      tokenBUSDpool: '0x499B6E03749B4bAF95F9E70EeD5355b138EA6C31',
      disabledB: 'disabled',
      disabledU: 'disabled',
      disabledBU: 'disabled',
    })
  }

  isU = async () => {
    this.setState({
      isU: '1',
      tokenUSD: this.state.tokenB,
      tokenBUSDpool: this.state.tokenABpool,
      tokenABpool: '',
      tokenB: '',
      disabledB: '',
      disabledU: 'disabled',
      disabledBU: 'disabled',
    })
  }

  addToken = async () => {
    if (this.state.isU === '1') {
      this.setState({
        tokenBUSDpool: this.state.tokenABpool,
        tokenUSD: this.state.tokenB
      })
    }
    await HangQin.methods.addToken(
      this.state.tokenA,
      this.state.tokenB,
      this.state.tokenUSD,
      this.state.tokenABpool,
      this.state.tokenBUSDpool
    ).send({ from: window.defaultAccount });
  }

  userModify = async () => {
    if (this.state.isU === '1') {
      this.setState({
        tokenBUSDpool: this.state.tokenABpool,
        tokenUSD: this.state.tokenB
      })
    }
    await HangQin.methods.userModify(
      this.state.tokenA,
      this.state.tokenB,
      this.state.tokenUSD,
      this.state.tokenABpool,
      this.state.tokenBUSDpool
    ).send({ from: window.defaultAccount });
  }


  render() {

    const content1 = (
      <div>
        
      </div>
    );
    const content2 = (
      <div>
        <p>A</p>
      </div>
    );
    const content3 = (
      <div>
        <p>B</p>
      </div>
    );
    const content4 = (
      <div>
        <p>AB</p>
      </div>
    );
    const content5 = (
      <div>
        <p>BU</p>
      </div>
    );

    return (
      <div>
        <Card type="inner" size='small' title=""
          style={{ borderRadius: '10px' }}
          headStyle={{ borderTopLeftRadius: '10px', borderTopRightRadius: '10px' }}
        >
          <div style={{ marginTop: '5px' }}>A
            <Popover content={content1} trigger="click">
              <QuestionCircleOutlined />
            </Popover>
          </div>
          <Input value={this.state.tokenA} placeholder=""
            onChange={event => { this.setState({ tokenA: event.target.value }) }} />
          <div style={{ marginTop: '5px' }}>B
            <Popover content={content2} trigger="click">
              <QuestionCircleOutlined />
            </Popover>&emsp;
            <Button type="primary" size="small" style={{ marginTop: '6px' }}
              onClick={() => { this.isHT() }}>B=HT</Button>&emsp;
            <Button type="primary" size="small" style={{ marginTop: '6px' }}
              onClick={() => { this.isU() }}>B=</Button>
          </div>
          <Input value={this.state.tokenB} placeholder=""
            disabled={this.state.disabledB}
            onChange={event => { this.setState({ tokenB: event.target.value }) }} />
          <div style={{ marginTop: '5px' }}>U
            <Popover content={content3} trigger="click">
              <QuestionCircleOutlined />
            </Popover>
          </div>
          <Input value={this.state.tokenUSD} placeholder=""
            disabled={this.state.disabledU}
            onChange={event => { this.setState({ tokenUSD: event.target.value }) }} />

          <div style={{ marginTop: '5px' }}>A/B
            <Popover content={content4} trigger="click">
              <QuestionCircleOutlined />
            </Popover>
          </div>
          <Input value={this.state.tokenABpool} placeholder="A/B"
            onChange={event => { this.setState({ tokenABpool: event.target.value }) }} />

          <div style={{ marginTop: '5px' }}>B/U
            <Popover content={content5} trigger="click">
              <QuestionCircleOutlined />
            </Popover>
          </div>
          <Input value={this.state.tokenBUSDpool} placeholder="B/U"
            disabled={this.state.disabledBU}
            onChange={event => { this.setState({ tokenBUSDpool: event.target.value }) }} />

          <Button shape='round' block type="primary" size="middle" style={{ marginTop: '6px' }}
            onClick={() => { this.addToken() }}></Button>

          <Button shape='round' block type="primary" size="middle" style={{ marginTop: '10px' }}
            onClick={() => { this.userModify() }}></Button>
        </Card>
      </div>
    );
  }
}

export default Hangqinadd;
