import React, { Component } from 'react';
import '../../App.css';
import '../../components/global';
import { Card, Button, Input, Divider } from 'antd';
import LGC from '../../ABI/LGC';
import LGCget from '../../ABI/LGCget';
import web3 from "../../ABI/web3";
import LG from '../../ABI/LG';
import Loading from '../../components/loadingReport';

class Hangqinadd extends Component {
  state = {
    userAdr: '',
    value: '',
    message: "",
    LGCclose: 'none',
    invite: '0x0000000000000000000000000000000000000001', //
    LGCTransferClose: 'none',
  };

  async componentDidMount() {
    //LGC
    const LGCbalance = await LGC.methods.balanceOf(window.defaultAccount).call();
    this.setState({ LGCbalance: LGCbalance });
    if (Number(LGCbalance) < 10) {
      this.setState({ LGCclose: 'block' })
    } else {
      this.setState({ LGCclose: 'none' })
    }

    //
    const invite = {};
    const _invite = window.location.search;
    const reg = /[?&][^?&]+=[^?&]+/g;
    const found = _invite.match(reg);
    if (found) {
      found.forEach(item => {
        let temp = item.substring(1).split('=');
        let key = temp[0];
        let value = temp[1];
        invite[key] = value;
      })
    }
    if (invite.address !== undefined) {
      this.setState({ invite: invite.address })
    }

    const isApprover = await LGC.methods.approverMap(window.defaultAccount).call()
    if (isApprover === true) {
      this.setState({ LGCTransferClose: 'block' })
    }

  }

  transfer = async () => {
    this.setState({ message: '' });
    const balance = await LGC.methods.balanceOf(this.state.userAdr).call();
    if (balance === '0') {
      await LGC.methods.transfer(this.state.userAdr, '10')
        .send({ from: window.defaultAccount })
    }
    if (balance >= '10') {
      this.setState({ message: '' + balance + '' });
    }
    if (balance < '10' && balance !== '0') {
      this.setState({ message: '' + balance + '' });
    }
  }

  getLGC = async () => {
    this.setState({ message: '' });
    const _LGbalance = await LG.methods.balanceOf(this.state.invite).call();
    const LGbalance = parseFloat(web3.utils.fromWei(_LGbalance, "ether")).toFixed(2);
    if (this.state.invite.toLowerCase() !== '0x0000000000000000000000000000000000000001') {
      if (LGbalance <= 8) {
        this.setState({ message: 'LG8' })
      }
    }
    if (this.state.invite.toLowerCase() === window.defaultAccount.toLowerCase()) {
      this.setState({ message: '' })
    }
    if (this.state.invite.toLowerCase() !== window.defaultAccount.toLowerCase() && LGbalance >= 8) {
      this._getLGC();
    }
    if (this.state.invite.toLowerCase() === '0x0000000000000000000000000000000000000001') {
      this._getLGC();
    }
  }

  _getLGC = async () => {
    await LGCget.methods.getLGC(this.state.invite).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.loading.loadingOpen('block', 'none', 'none', "")
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.loading.loadingOpen('none', 'block', 'none', ",24(10)")
        }
      })
      .on('error', (error, receipt) => {
        if (receipt !== undefined && receipt.status === false) {
          this.loading.loadingOpen('none', 'none', 'block', "")
        }
      })
  }

  render() {

    return (
      <div>
        <Card type="inner" size='small' >
          <Divider >{this.state.value}</Divider>
          <div>
            <p><b></b>
              <div>&nbsp;&nbsp;&nbsp;&nbsp;
                10,
                :LGC&ge;10
              </div>
            </p>
            <p><b></b>
              <div>&nbsp;&nbsp;&nbsp;&nbsp;2410
                ()
              </div>
            </p>
            <p><b></b>
              <div>1.</div>
              <div>2.</div>
              <div>24</div>
            </p>
            <p><b></b>
              <div>
                <div>1.,9</div>
              </div>
            </p>
            <div style={{ display: this.state.LGCclose, marginTop: '10px' }} >
              <Button onClick={() => { this.getLGC() }}
                type="primary"
                size='larger'
                shape='round'
                block >
                
              </Button>
              <div style={{ color: "green", fontSize: "12px" }}>
                {this.state.message}
              </div>
            </div>
          </div>
          <div style={{ display: this.state.LGCTransferClose }}>
            <Divider >()</Divider>
            <div style={{ color: 'blue' }}>
              <b></b><br />
              <b> 24</b>
            </div>
            <div style={{ marginTop: '5px' }}>
            </div>
            <Input value={this.state.userAdr} placeholder=""
              onChange={event => { this.setState({ userAdr: event.target.value }) }} />
            <Button shape='round' block type="primary" size="middle" style={{ marginTop: '6px' }}
              onClick={() => { this.transfer() }}></Button>
            <div style={{ color: "green", fontSize: "13px", textAlign: 'center' }}>
              {this.state.message}
            </div>
          </div>
        </Card>
        <Loading ref={loadwindow => { this.loading = loadwindow }} />
      </div>
    );
  }
}

export default Hangqinadd;
