import React, { Component } from 'react';
import ReactECharts from 'echarts-for-react';
import { Card } from 'antd';

class price extends Component {
  constructor(props) {
    super(props)

    this.state = {

    }
  }

  componentDidMount() {



  }

  render() {
    const options = {

      tooltip: {
        trigger: 'axis',
        axisPointer: {      // 
          type: 'shadow'    // 'line' | 'shadow'
        }
      },

      grid: {
        top: '3%',
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: {
        type: 'category',
        splitLine: { show: false },
        data: ["111", "112", "113", "114", "115", "116", "117", "118", "119", "1110", "1111"]
      },
      yAxis: {
        type: 'value'
      },
      series: [
        {
          name: '',
          type: 'bar',
          stack: 'price',
          itemStyle: {
            normal: {
              barBorderColor: 'rgba(0,0,0,0)',
              color: 'rgba(0,0,0,0)'
            },
            emphasis: {
              barBorderColor: 'rgba(0,0,0,0)',
              color: 'rgba(0,0,0,0)'
            }
          },
          data: [0, 900, 1245, 1530, 1376, 1376, 1511, 1689, 1856, 1495, 1292]
        },
        {
          name: '',
          type: 'bar',
          stack: 'price',
          color: 'rgb(3,173,144)',
          data: [900, 345, 393, 0, 0, 135, 178, 286, 0, 0, 0]
        },
        {
          name: '',
          type: 'bar',
          stack: 'price',
          color: 'rgb(209,75,100)',
          data: [0, 0, 0, 108, 154, 0, 0, 0, 119, 361, 203]
        }

      ]
    };
    return (
      <div>
        <Card size='small' >
          <ReactECharts option={options} />;
        </Card>
      </div>
    )
  }
}
export default price;