import React, { Component } from 'react'
import {Card ,Button} from 'antd';

export class introduce extends Component {
    constructor(props) {
        super(props)
        this.state = {
            open:this.props.open
        }
    }
    
    open=()=>{
        if(this.state.open==='none'){
          this.setState({open:'block'})
        }else{
          this.setState({open:'none'})
        }
    }

    render() {
        return (
            <div>
                <Card type="inner" size='small' style={{borderRadius:'10px'}} >
                    <Button shape='round' type="dashed" block  size="small"  onClick={() => {this.open()}}></Button>
                    <div style={{display:this.state.open}}>
                        <div>
                            <div style={{marginTop:'5px'}}><b></b></div>
                            <div>
                                LG
                                
                                !!
                            </div>
                            <div style={{marginTop:'5px'}}><b></b></div>
                            <div>OK
                                
                                ETH
                                
                            </div>
                            <div style={{marginTop:'5px'}}><b></b></div>
                            <div>5
                            </div>
                            <div style={{marginTop:'5px'}}><b></b></div>
                            <div>30%LG
                                
                            </div>
                        </div>
                    </div>
                </Card>
            </div>
        )
    }
}

export default introduce
