  lockFind= async (AllLocks)=>{
    const multicall = new Multicall({
      web3Instance: web3, tryAggregate: true,
      multicallCustomContractAddress: this.state.multicallAdr
    });
    const calls = [];
    for (let i = 0; i < AllLocks.length; i++) {
      calls.push(
        {
          reference: i,
          contractAddress: AllLocks[i],
          abi: LockABI.abi,
          calls: [
            {
              reference: 'user',
              methodName: 'owner',
              methodParameters: []
            },
            {
              reference: 'Admin',
              methodName: 'admin',
              methodParameters: []
            }
          ]
        }
      )
    }
    const Call = await multicall.call(calls)
    return Call;
  }

