  set = async () => {
    const Lock = new web3.eth.Contract(LockABI.abi, this.state.lockAdr);
    const ERC20 = new web3.eth.Contract(erc20abi.abi, this.state.tokenAdr);
    const decimals = await ERC20.methods.decimals().call();
    const amount = new BigNumber(this.state._amount / 10 ** (18 - decimals)).toFixed();
    await Lock.methods.set(
      this.state._addDay,
      this.state._day,
      web3.utils.toWei(amount, 'ether'),
    ).send({ from: window.defaultAccount })
      .on('transactionHash', (transactionHash) => {
        if (transactionHash.length !== 0) {
          this.setState({ message: "" });
        }
      })
      .on('receipt', (receipt) => {
        if (receipt.status === true) {
          this.setState({ message: "" });
        }
      });
    const amount2 = await Lock.methods.amount().call();
    const getTime2 = await Lock.methods.getTime().call();
    const day2 = await Lock.methods.day().call();
    this.setState({
      time: String(Math.ceil(getTime2 / 3600 / 24)),
      amount: String((web3.utils.fromWei(amount2, 'ether')) * 10 ** (18 - decimals).toFixed(0)),
      day: day2,
      time2: String(Math.ceil(getTime2 / 3600 / 24)),
    })
    this.componentDidMount();
  }

