import React, { Component } from 'react'
import { connect } from "react-redux"
import { Toast } from 'antd-mobile'
import { Button } from 'semantic-ui-react'
import ChainSelectIDfrom from '../../../../components/ChainSelect/chainSelectIDfrom'
import ChainSelectIDto from '../../../../components/ChainSelect/chainSelectIDto'
import TokensSelect from "./tokensSelect"
import { getChainInfo, SelectWeb3 } from '../../../../components/global/functions';
import bridgeAbi from '../../../../ABI/Bridge.abi.json';
import { Multicall } from 'ethereum-multicall';
const abi = require('ethereumjs-abi')
var autoNum 
const maxQueryBlock=2000  //


export class Auto extends Component {
  constructor(props) {
    super(props)
    this.state = {
      ChainTo: '',
      ToWeb3: '',
      FromWeb3: '',
      ChainFrom: '',
      FromBridgeAdr: '',
      ToBridgeAdr: '',
      selectCoinOrAdr: '',
      multicallAdr: '',
      playing: 'stop',
      num: 0,
      message:''
    }
  }

  async componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.selectCoinOrAdr !== this.props.selectCoinOrAdr) {
      if (nextProps.selectCoinOrAdr !== '' && nextProps.selectCoinOrAdr !== undefined) {
        this.setState({ selectCoinOrAdr: nextProps.selectCoinOrAdr })
      }
    }
  }

  async componentDidMount() {
    if (this.props.selectCoinOrAdr !== '' && this.props.selectCoinOrAdr !== undefined) {
      this.setState({ selectCoinOrAdr: this.props.selectCoinOrAdr })
      console.log(this.props.selectCoinOrAdr)
    }
  }

  chainSelectFrom = async (chainId) => {
    const FromWeb3 = await SelectWeb3(chainId)
    await getChainInfo(chainId).then((result) => {
      this.setState({
        FromBridgeAdr: result.ChainAdr.bridge
      })
    })
    this.setState({ FromWeb3, ChainFrom: chainId })
  }

  chainSelectIDto = async (chainId) => {
    const ToWeb3 = await SelectWeb3(chainId)
    await getChainInfo(chainId).then((result) => {
      this.setState({
        ToBridgeAdr: result.ChainAdr.bridge,
        multicallAdr: result.ChainAdr.multicall
      })
    })
    this.setState({ ToWeb3, ChainTo: chainId })
  }

  autoStart = () => {
    let state = this.state
    this.seeHistory()
    console.log(this.props.times)
    if (state.playing === 'stop') {
      this.setState({ playing: 'start' })
      autoNum = setInterval(() => {
        console.log('start')
        this.seeHistory()
      }, this.props.times)
      return Toast.show({ content: '' })
    } else {
      this.setState({ playing: 'stop' })
      clearInterval(autoNum)
      console.log('stop')
      return Toast.show({ content: '' })
    }
  }

  seeHistory = async () => {
    let state = this.state
    this.setState({ num: state.num + 1,message:'' })
    const bridgeContract = new state.FromWeb3.eth.Contract(bridgeAbi.abi, state.FromBridgeAdr)
    let topicsAdr = '0x' + (Array(64).join(0) + state.selectCoinOrAdr.slice(2)).slice(-64)
    let lastBlock
    let fromBlock
    let toBlock
    await state.FromWeb3.eth.getBlockNumber().then((block) => { lastBlock = block });
    fromBlock = lastBlock - maxQueryBlock
    toBlock = lastBlock
    await bridgeContract.getPastEvents('Lock', {
      fromBlock: fromBlock,
      toBlock: toBlock,
      topics: [null, topicsAdr, null, null]
    },
      async (error, events) => {
        console.log(error)
        if (events !== undefined && events.length !== 0) {
          let moreCode = ''
          this.setState({message:`${events.length}`})
          for (var i = events.length - 1; i >= 0; i--) {
            let event = events[i].returnValues
            let bridgeCode = event.FromChain + '&' + event.ToChain + '&'
              + event.tokenAdr + '&' + event.who + '&' + event.time + '&'
              + event.value + '&' + event.password
            let isMore = moreCode === '' ? '' : ','
            moreCode = bridgeCode + isMore + moreCode
          }
          this.filter(moreCode)
        }
      })
  }

  //
  filter = async (inputData) => {
    let state = this.state
    this.setState({message:''})
    //
    let moreCode = inputData.split(',')
    //
    let MoreCode = []
    for (let i = 0; i < moreCode.length; i++) {
      let _moreCode = moreCode[i].split('&')
      MoreCode.push(_moreCode)
    }
    //TO
    let curChain = parseInt(state.ChainTo, 16) + ''
    let curChainCode = []
    for (let i = 0; i < MoreCode.length; i++) {
      if (MoreCode[i][1] === curChain) {
        curChainCode.push(MoreCode[i])
      }
    }
    // 
    const multicall = new Multicall({
      web3Instance: state.ToWeb3, tryAggregate: true,
      multicallCustomContractAddress: state.multicallAdr
    });
    let _calls = []
    for (let i = 0; i < curChainCode.length; i++) {
      let call = {
        reference: i,
        methodName: 'isUNlockToken',
        methodParameters: [curChainCode[i][6]]
      }
      _calls.push(call)
    }
    const call1 = [{
      reference: 'isUNlockToken',
      contractAddress: state.ToBridgeAdr,
      abi: bridgeAbi.abi,
      calls: _calls
    }]
    const Call = await multicall.call(call1)
    let callResult = Call.results.isUNlockToken.callsReturnContext
    let noSendCode = []
    for (let i = 0; i < callResult.length; i++) {
      if (callResult[i].returnValues[0] === false) {
        noSendCode.push(curChainCode[callResult[i].reference])
      }
    }
    if (noSendCode.length === 0) {
      console.log('')
      this.setState({message:''})
      return
    }
    //
    if (noSendCode.length === 1) {
      this.onceSend(noSendCode)
    } else {
      this.moreSend(noSendCode)
    }
  }

  onceSend = async (inputData) => {
    let state = this.state
    this.setState({message:'1'})
    //
    let paramsData = abi.rawEncode(["uint", "uint", "address",
      "address", "uint", "uint", "uint"],
      [inputData[0][0], inputData[0][1], inputData[0][2], inputData[0][3], 
      inputData[0][4],inputData[0][5], inputData[0][6]]).toString('hex')
    let data = "0xb3632952" + paramsData
    await state.ToWeb3.eth.accounts.signTransaction({
      to: state.ToBridgeAdr,  //
      value: '0x0',  //payableethvalue
      gas: 200000,
      data: data
    }, this.props.password)
      .then(async (ts) => {
        console.log(ts)
        this.setState({message:'1'})
        await state.ToWeb3.eth.sendSignedTransaction(ts.rawTransaction)
          .on('receipt', (receipt) => {
            console.log(receipt, 'ok')
            console.log(receipt.transactionHash)
            this.props.record(receipt.transactionHash)
            this.setState({message:'1'})
          })
          .on('error', (error)=>{
            console.log(error)
            this.setState({message:'1'})
          })
      })
  }

  moreSend = async (inputData) => {
    this.setState({message:`${inputData.length}`})
    let state = this.state
    //
    let FromChain = []
    let ToChain = []
    let _tokenAdr = []
    let who = []
    let time = []
    let value = []
    let password = []
    for (let i = 0; i < inputData.length; i++) {
      FromChain.push(inputData[i][0])
      ToChain.push(inputData[i][1])
      _tokenAdr.push(inputData[i][2])
      who.push(inputData[i][3])
      time.push(inputData[i][4])
      value.push(inputData[i][5])
      password.push(inputData[i][6])
    }
    //
    let paramsData = await abi.rawEncode(["uint[]", "uint[]", "address[]",
      "address[]", "uint[]", "uint[]", "uint[]"],
      [FromChain, ToChain, _tokenAdr, who, time, value, password]).toString('hex')
    let data = "0xd2eee910" + paramsData
    await state.ToWeb3.eth.accounts.signTransaction({
      to: state.ToBridgeAdr,  //
      value: '0x0',  //payableethvalue
      gas: 5000000,
      data: data
    }, this.props.password)
      .then(async (ts) => {
        console.log(ts)
        this.setState({message:`${inputData.length}`})
        await state.ToWeb3.eth.sendSignedTransaction(ts.rawTransaction)
          .on('receipt', (receipt) => {
            console.log(receipt, 'ok')
            console.log(receipt.transactionHash)
            this.props.record(receipt.transactionHash)
            this.setState({message:`${inputData.length}`})
          })
          .on('error', (error)=>{
            console.log(error)
            this.setState({message:`${inputData.length}`})
          })
      })
  }

  render() {
    let playing = this.state.playing
    return (
      <div>
        <div style={{ textAlign: 'center' }}>
          <Button.Group >
            <Button icon disabled={playing === 'start' ? true : false}>
              <TokensSelect />
            </Button>
            <Button icon disabled={playing === 'start' ? true : false}>
              <ChainSelectIDfrom chainSelectFrom={this.chainSelectFrom} />
            </Button>
            <Button.Or text={this.state.num} />
            <Button icon disabled={playing === 'start' ? true : false}>
              <ChainSelectIDto chainSelectIDto={this.chainSelectIDto} />
            </Button >
            <Button positive icon='play' loading={playing === 'start' ? true : false}
              onClick={() => this.autoStart()}
            />
          </Button.Group>
          <div>{this.state.message}</div>
        </div>

      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    selectCoinOrAdr: state.selectCoinOrAdr
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Auto) 
