import React, { Component } from 'react';
import '../../App.css';
import web3 from "../../ABI/web3";
import anyAirdrop from '../../ABI/anyAirdrop';
import { Select, Input, Button, Divider, Card } from 'antd';
import LGCwindow from '../../components/LGCwindow';
import LG from '../../ABI/LG';
import Loading from '../../components/loadingReport';
import '../../components/global';
const { Option } = Select;
const BigNumber = require('bignumber.js');

class STAirdrop extends Component {

  state = {
    AirdropAdr: "",  //YHT
    AirdropAmount: "",
    AirdropTotal: "",
    ADDAirdropTotal: '',
    ApproveTotal: "",
    anyAirdropaddr: global.constants.anyAirdrop,  //
    erc20abi: "",
    AirdropinfoAmount: "",
    ERC20symbol: "",
    AirdropBalance: "",
    modifyAirdropAmount: "",
    message: "",
    onoff2: 'none',
    disabled: '',

  };

  async componentDidMount() {
 
    const erc20abi = require("../../ABI/ERC20.abi.json");
    this.setState({ erc20abi });
  }

  Approve = async () => {
    const ERC20 = new web3.eth.Contract(this.state.erc20abi.abi, this.state.AirdropAdr);
    console.log(ERC20);
    await ERC20.methods.approve(this.state.anyAirdropaddr, web3.utils.toWei(this.state.ApproveTotal, 'ether'))
      .send({ from: window.defaultAccount });
  }

  giveAirdrop = async () => {
    await anyAirdrop.methods.giveAirdrop(this.state.AirdropAdr, web3.utils.toWei(this.state.AirdropAmount, 'ether'), web3.utils.toWei(this.state.AirdropTotal, 'ether'))
      .send({ from: window.defaultAccount });
  }

  getAirdrop = async () => {
    const _LGbalance = await LG.methods.balanceOf(window.defaultAccount).call();
    const LGbalance = await new BigNumber(web3.utils.fromWei(_LGbalance, "ether")).toFixed(0);
    if (Number(window.usertotal) > Number(LGbalance)) {
      this.loading.loadingOpen('none', 'none', 'block', "LGLGLGLG,")
    } else {
      if (Number(window.userLGC) < 10) {
        this.LGCwindow.Open("'LGC'")
      } else {
        this.setState({ message: "" });
        await anyAirdrop.methods.getAirdrop(this.state.AirdropAdr).send({ from: window.defaultAccount });
        this.setState({ message: "" });
      }
    }
  }

  claim = async () => {
    await anyAirdrop.methods.claim(this.state.AirdropAdr).send({ from: window.defaultAccount });
  }

  modifyAirdropAmount = async () => {
    await anyAirdrop.methods.modifyAirdropAmount(this.state.AirdropAdr, web3.utils.toWei(this.state.modifyAirdropAmount, 'ether')).send({ from: window.defaultAccount });
  }


  getAirdropinfo = async () => {
    const ERC20 = await new web3.eth.Contract(this.state.erc20abi.abi, this.state.AirdropAdr);
    await ERC20.methods.symbol().call().then(ERC20symbol => { this.setState({ ERC20symbol }) });
    const AirdropBalance = await ERC20.methods.balanceOf(this.state.anyAirdropaddr).call();
    this.setState({ AirdropBalance: parseFloat(web3.utils.fromWei(AirdropBalance, 'ether')).toFixed(2) })
    const airInfo = await anyAirdrop.methods.getAirTokenInfo(this.state.AirdropAdr).call();
    const _AirdropinfoAmount = parseFloat(web3.utils.fromWei(airInfo[1], "ether")).toFixed(2);
    if (AirdropBalance === '0') {
      this.setState({ AirdropinfoAmount: 0 })
      this.setState({ disabled: 'disabled' })
    } else {
      this.setState({ AirdropinfoAmount: _AirdropinfoAmount })
      this.setState({ disabled: '' })
    }
  }

  transfer = async () => {
    const ERC20 = await new web3.eth.Contract(this.state.erc20abi.abi, this.state.AirdropAdr);
    await ERC20.methods.transfer(
      this.state.anyAirdropaddr, web3.utils.toWei(this.state.ADDAirdropTotal, 'ether'))
      .send({ from: window.defaultAccount });
  }

  handleChange = (value) => {
    console.log(`${value}`);
    this.setState({ AirdropAdr: `${value}` });
  }

  open2 = () => {
    this.setState({ onoff2: "block" });
  }

  close2 = () => {
    this.setState({ onoff2: "none" });
  }
  render() {

    const children = [];
    children.push(
      <Option key={'0x7cc0c1a34b1ae55f9bd2a19ece9d4b166b66e70c'}>LG</Option>,
      <Option key={'0x158c9af12137cF8dfd86b3bD191DbAB79D41C856'}>YHT</Option>,
      <Option key={'0x98812b562a8684910575c22c40364ecb7bdb5bfb'}>DKJ</Option>,
      <Option key={'0xb04Fe6D9187B8bae328Ce2bE89ea9F6Bf906fC4a'}>FHB</Option>,
      <Option key={'0x00168d3B2ca893B2788399AaFCfA0E166f8eDC41'}>HXB</Option>,
      <Option key={'0xf4ba6c6e40ad12e2112e4cf77f87de9bc101b59e'}>DB</Option>,
      <Option key={'0x1935F765D492844857aEDa50bc5c27fC56A9858a'}>WKT</Option>
    );

    return (
      <div >
        <Card size='small' >
          <Button shape='round' type="dashed" block size="middle" onClick={() => { this.open2() }}></Button>
          <div style={{ display: this.state.onoff2 }}>
            <p>
              
            </p>
            <p>,</p>
            <p>0</p>
            <div style={{ color: "red", marginTop: '10px' }}>
              
            </div>
            <Button shape='round' block type="dashed" size="middle" onClick={() => { this.close2() }}></Button>
          </div>
          <div>
            <Divider>(LGC&ge;10,&nbsp;LG&ge;{parseFloat(window.usertotal).toFixed(0)})</Divider>
            <div>
              <div>
                <p>:
                  <Select style={{ width: '30%' }} onChange={this.handleChange}>
                    {children}
                  </Select>
                  <Input style={{ width: '50%' }} value={this.state.AirdropAdr} placeholder=""
                    onChange={event => { this.setState({ AirdropAdr: event.target.value }) }} />
                </p>
                <p style={{ color: "blue", marginBottom: '10px' }}>
                  {this.state.ERC20symbol}&nbsp;&nbsp;&nbsp;&nbsp;
                  {this.state.AirdropinfoAmount}&nbsp;&nbsp;&nbsp;&nbsp;
                  {this.state.AirdropBalance}&nbsp;&nbsp;&nbsp;&nbsp;
                </p>
              </div>
              <sapn  >
                <Button style={{ width: '50%' }} shape='round' type="primary" size='large'
                  onClick={() => { this.getAirdropinfo() }}></Button>
                <Button danger style={{ width: '50%' }} shape='round' type="primary" size='large'
                  onClick={() => { this.getAirdrop() }}
                  disabled={this.state.disabled}
                ></Button>
              </sapn>
            </div>
            <div style={{ color: "green", fontSize: "10px" }}>
              {this.state.message}
            </div>
          </div>
        </Card>
        <Card size='small'>
          <Divider></Divider>
          <p>
            <Input style={{ width: "305px" }} value={this.state.AirdropAdr}
              placeholder=""
              onChange={event => { this.setState({ AirdropAdr: event.target.value }) }} />
          </p>
          <p>
            
            <Input style={{ width: "120px" }} value={this.state.ApproveTotal}
              placeholder=""
              onChange={event => { this.setState({ ApproveTotal: event.target.value }) }} />
            <Button danger shape='round' style={{ float: "right" }}
              onClick={() => { this.Approve() }}
            ></Button>
          </p>
          <p>
            
            <Input style={{ width: "120px" }} value={this.state.AirdropAmount}
              placeholder=""
              onChange={event => { this.setState({ AirdropAmount: event.target.value }) }} />
          </p>
          <p>
            
            <Input style={{ width: "120px" }} value={this.state.AirdropTotal}
              placeholder=""
              onChange={event => { this.setState({ AirdropTotal: event.target.value }) }} />
            <Button danger shape='round' block style={{ float: "right", marginTop: '10px', marginBottom: '15px' }}
              onClick={() => { this.giveAirdrop() }}
            ></Button>
          </p>
          <Divider />
          <p>
            <Input style={{ width: "120px" }} value={this.state.ADDAirdropTotal}
              placeholder=""
              onChange={event => { this.setState({ ADDAirdropTotal: event.target.value }) }} />
            <Button danger style={{ float: "right" }} shape='round'
              onClick={() => { this.transfer() }}
            ></Button>
          </p>
          <p>
            <Input style={{ width: "120px" }} value={this.state.modifyAirdropAmount}
              placeholder=""
              onChange={event => { this.setState({ modifyAirdropAmount: event.target.value }) }} />
            <Button danger style={{ float: "right" }} shape='round'
              onClick={() => { this.modifyAirdropAmount() }}
            ></Button>
          </p>
          <Divider ></Divider>
          <Button danger shape='round' block onClick={() => { this.claim() }}>()</Button>
        </Card>
        <LGCwindow ref={LGCwindow => { this.LGCwindow = LGCwindow }} />
        <Loading ref={loadwindow => { this.loading = loadwindow }} />
      </div>
    );
  }
}


export default STAirdrop;
