import React, { Component } from 'react'
import { connect } from "react-redux"
import { Card } from 'antd';
import { Toast } from 'antd-mobile'
import { Input, Button, Popup, Table, Dropdown } from 'semantic-ui-react'
import Head from './components/headAdmin';
import Auto from './components/auto';
import { CopyToClipboard } from 'react-copy-to-clipboard';

export class bridgeAuto extends Component {
  constructor(props) {
    super(props)
    this.state = {
      password: '',
      autoList: [],
      record: [],
      copy: '',
      timesDisabled:false,
      times:'30000',
    }
  }

  add = () => {
    let state = this.state
    if (state.password === '') {
      return Toast.show({ content: '' })
    }
    const autoList = state.autoList
    autoList.push(
      <Table.Row textAlign='center' style={{ paddingBottom: '5px', paddingTop: '5px' }}>
        <Table.Cell><Auto password={state.password} times={state.times} record={this.record} /></Table.Cell>
      </Table.Row>
    )
    this.setState({ autoList, timesDisabled:true })
  }

  copyTips = () => {
    return Toast.show({
      content: '',
    })
  }

  record = (has) => {
    let state = this.state
    const record = state.record
    record.unshift(
      <Table.Row textAlign='center' style={{ paddingBottom: '5px', paddingTop: '5px' }}>
        <CopyToClipboard text={has} >
          <Table.Cell onClick={() => this.copyTips()}>
            {has}
          </Table.Cell>
        </CopyToClipboard>
      </Table.Row>
    )
    this.setState({ record })
  }

  render() {
    let state = this.state
    let times = [
      { key: '0', text: '0.5', value: '30000' },
      { key: '1', text: '1', value: '60000' },
      { key: '2', text: '2', value: '120000' },
      { key: '3', text: '3', value: '180000' },
      { key: '4', text: '5', value: '300000' },
      { key: '5', text: '10', value: '600000' },
    ]
    return (
      <div>
        <Head />
        <Card type="inner" size='small' className='bridgeUser' style={{ maxWidth: '500px', margin: 'auto auto 2px auto' }}>
          <div style={{ textAlign: 'center', margin: '0 auto 5px auto' }}>
            <Popup
              trigger={
                <Input style={{ marginBottom: '5px' }}
                  value={state.password}
                  onChange={event => { this.setState({ password: event.target.value }) }}
                  disabled={state.timesDisabled}
                  label={
                    <Popup
                      trigger={
                        <Button></Button>
                      }
                      content=','
                      inverted
                      position='top right'
                      on='click'
                    />
                  }
                  labelPosition='left '
                  fluid
                  placeholder=''
                  actionPosition='left'
                />
              }
              content='(GAS)
              '
              inverted
              on='focus'
            />
            <Button.Group >
              <Popup
                trigger={
                  <Popup
                    trigger={
                      <Button icon>()</Button>
                    }
                    content=''
                    inverted
                    position='top right'
                    on='click'
                  />
                }
                content='(GAS)
              '
                inverted
                on='focus'
              />
              <Button primary disabled={state.timesDisabled}>
                <Dropdown
                  defaultValue='30000' options={times}
                  onChange={(e, { value }) => this.setState({times:value})}
                />
              </Button>
            </Button.Group>
          </div>
          <Table inverted>
            <Table.Body >
              {state.autoList}
            </Table.Body>
          </Table>
          <div style={{ textAlign: 'center', margin: '0 auto 5px auto' }}>
            <Button.Group >
              <Button size='mini' primary onClick={() => { this.add() }}>+</Button>
            </Button.Group>
            <div style={{ clear: 'both' }} />
          </div>

          <Table celled inverted selectable fixed singleLine unstackable='false' style={{ background: 'rgba(255,255,255,0.1)' }}>
            <Table.Header >
              <Table.Row textAlign='center'>
                <Popup
                  trigger={<Table.HeaderCell></Table.HeaderCell>}
                  content=''
                  inverted
                  on='click'
                />
              </Table.Row>
            </Table.Header>
            <Table.Body >
              {state.record}
            </Table.Body>
          </Table>
        </Card>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    selectCoinOrAdr: state.selectCoinOrAdr
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(bridgeAuto) 
