import React,{ Component }  from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Card ,Button,Input,Popover , Select,AutoComplete} from 'antd';
import { QuestionCircleOutlined} from '@ant-design/icons';
import Hongbao from '../../../ABI/hongbao';
import erc20abi from '../../../ABI/ERC20.abi.json';
import '../../../components/global';
import HangQinV3 from '../../../ABI/hangqin';
import { Group } from 'antd/lib/avatar';
import Loading from '../../../components/loadingReport';
import '../../../components/global';

const { Option } = Select;

class HBgive extends Component {
      state = {
        HBaddr:global.constants.hongbao,  //
        _HBtokenAdr:'',
        HBtokenAdr:'',
        approveTotal:'',
        HBtotal:'',
        HBamount:'',
        HBremark:'',
        HBremark2:'',
        HBlownBalance:'0',
        HBpassword:'0',
        HBid:'',
        _HTforFee:'', //HT
        HTforFee:'0',
        allowance:'',
        message:"",
        message1:'',
        message2:'',
        message3:'',
        Approvedisplay:'none',
        UserName:'',
        symbol:' ',
        tokenlist:[],
        open:'none',
        disabled:''
      };

  async componentDidMount(){

    //HT
    const _HTforFee = await Hongbao.methods.HTforFee().call();
    this.setState({_HTforFee});
    await this.list();
  }

  list= async()=>{
    const AllTokensAdr = await HangQinV3.methods.AllTokens().call();
    const AllTokensName = await HangQinV3.methods.AllTokensName().call();
    const _list = [];
    for(var i=0; i<AllTokensAdr.length; i++){
      _list.push(
      <Option key={AllTokensAdr[i]}>{AllTokensName[i]}</Option>)

    }
    this.setState({tokenlist:_list})
  }


  Approve = async () =>{
    const ERC20 = await new web3.eth.Contract(erc20abi.abi,this.state.HBtokenAdr);
    await ERC20.methods.approve(this.state.HBaddr, web3.utils.toWei('10000000000000000000','ether'))
    .send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.loading.loadingOpen('block','none','none',"")
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.loading.loadingOpen('none','block','none',"")
        this.setState({disabled:'',Approvedisplay:'none'});
      }
    })
    .on('error',(error,receipt)=>{
      if(receipt !== undefined && receipt.status === false){
        this.loading.loadingOpen('none','none','block',"")
      }
    })
  }

  changUserName = async () =>{
    await Hongbao.methods.changUserName(this.state.UserName).send({from:window.defaultAccount})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.loading.loadingOpen('block','none','none',"")
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.loading.loadingOpen('none','block','none',"")
        this.setState({disabled:''});
      }
    })
    .on('error',(error,receipt)=>{
      if(receipt !== undefined && receipt.status === false){
        this.loading.loadingOpen('none','none','block',"")
      }
    })
  }

  giveHB = async () =>{
    const tokenIsPayFee = await Hongbao.methods.tokenIsPayFee(this.state.HBtokenAdr).call();
    if(tokenIsPayFee === '1'){this.setState({HTforFee:this.state._HTforFee})};
    await Hongbao.methods.giveTokenHB(
        this.state.HBtokenAdr,
        web3.utils.toWei(this.state.HBtotal,'ether'),
        this.state.HBamount,
        this.state.HBremark,
        web3.utils.toWei(this.state.HBlownBalance,'ether'),
        this.state.HBpassword
        )
    .send({from:window.defaultAccount,value:this.state.HTforFee})
    .on('transactionHash',(transactionHash)=>{
      if(transactionHash.length !== 0){
        this.loading.loadingOpen('block','none','none',"")
      }
    })
    .on('receipt',(receipt)=>{
      if(receipt.status === true){
        this.loading.loadingOpen('none','block','none',"")
        this.setState({disabled:''});
      }
    })
    .on('error',(error,receipt)=>{
      if(receipt !== undefined && receipt.status === false){
        this.loading.loadingOpen('none','none','block',"")
      }
    })
  }

  getHBbalance =async () => {
    await Hongbao.methods.getHBbalance(this.state.HBid).send({from:window.defaultAccount});
  }

  modifyremark =async () => {
    await Hongbao.methods.modifyremark(this.state.HBid, this.state.HBremark2).send({from:window.defaultAccount});
  }

  getHBpassword =async () => {
    const passwordmessage = await Hongbao.methods.getHBpassword(this.state.HBid).call({from:window.defaultAccount});
    this.setState({message2:''+passwordmessage});
  }

  getHBlownBalance =async () => {
    const HBinfo = await Hongbao.methods.getHBinfo(this.state.HBid).call({from:window.defaultAccount});
    console.log(HBinfo)
    const ERC20 = await new web3.eth.Contract(erc20abi.abi,HBinfo[3]);
    const decimals = await ERC20.methods.decimals().call();
    this.setState({message2:''+ parseFloat(HBinfo[7]*10**(-decimals) ).toFixed(2) });
  }

  Select=(value)=>{
    this.setState({_HBtokenAdr:value})
  }
  handleChange =async () =>{
    this.state.HBtokenAdr=this.state._HBtokenAdr;
    const ERC20 = await new web3.eth.Contract(erc20abi.abi,this.state.HBtokenAdr);
    const _allowance = await  ERC20.methods.allowance(window.defaultAccount,this.state.HBaddr).call();
    const allowance = await web3.utils.fromWei(_allowance,'ether');
    this.setState({message1:""});
    if (allowance < 100000000){this.setState({Approvedisplay: 'block'}) }else{
      this.setState({Approvedisplay: 'none'}) 
    }
    this.confim();
  }

  confim = async() =>{
    const ERC20 =  new web3.eth.Contract(erc20abi.abi,this.state.HBtokenAdr);
    const _allowance = await ERC20.methods.allowance(window.defaultAccount,this.state.HBaddr).call();
    const allowance = web3.utils.fromWei(_allowance,'ether');
    const symbol = await ERC20.methods.symbol().call();
    this.setState({symbol})
    const decimals = await ERC20.methods.decimals().call();
    if(decimals !== '18'){
      this.setState({message1:"18"})
      this.setState({Approvedisplay: 'none'}) 
    }else{
      this.setState({message1:""})
            if (allowance < 100000000){
              this.setState({Approvedisplay: 'block',disabled:'disabled'}) 
            }else{
                  this.setState({Approvedisplay: 'none',disabled:''}) 
                }
         }
  }

  open=()=>{
    if(this.state.open==='none'){
      this.setState({open:'block'})
    }else{
      this.setState({open:'none'})
    }
  }

  render(){

      const content2 = (
        <div>
          &gt;100LG,
        </div>
      );

      const content3 = (
        <div>
          0
          
          
          
        </div>
      );

      const content4 = (
        <div>
          1001000
        </div>
      );

      const content6 = (
        <div>
          24
        </div>
      );

      const content7 = (
        <div>
          
        </div>
      );
      const content9 = (
        <div>
          
        </div>
      );
      const content10 = (
        <div>
          
        </div>
      );
      const content11 = (
        <div>
          
        </div>
      );
      const content12 = (
        <div>
          
        </div>
      );
      const content13 = (
        <div>
          
        </div>
      );

  return (
    <div>
        <Card size='small' style={{background:'rgb(254, 73, 82)',borderRadius:'10px'}}>
            <div style={{marginTop:'5px'}}>
              <Group style={{ width: '100%' }}>
                  <Input value={this.state.UserName} placeholder="()" 
                    addonBefore='' 
                    suffix={
                      <Popover content={content11} trigger="click">
                        <QuestionCircleOutlined />  
                      </Popover>
                    }
                    onChange={event=>{this.setState({UserName:event.target.value})}}
                  />
                  <sapn style={{float:'right'}}>
                      <Button  type="primary"  onClick={() => {this.changUserName()}}></Button>
                  </sapn>
                  </Group>
            </div>
            <div style={{color:"green",fontSize:"10px",textAlign:'center'}}>
                {this.state.message3}
            </div>
        </Card>
        <Card type="inner" size='small' style={{background:'rgb(254, 73, 82)',borderRadius:'10px'}} >
              <div>
                <Group style={{ width: '100%' }}>
                  <Button style={{textAlign:'center', width: '30%' }}>:<b style={{textAlign:'center'}}>{this.state.symbol}</b></Button>
                  <AutoComplete
                    style={{ width: '50%' }}
                    placeholder=""
                    onChange={this.Select}
                  >{this.state.tokenlist}</AutoComplete>
                  <Button style={{ width: '20%' }} type="primary"  onClick={this.handleChange}></Button>
                </Group>  
              </div>
              <div style={{marginTop:'3px',display:this.state.Approvedisplay}}>
                        <Button  block shape='round' type="primary"  size='middle' 
                          style={{borderWidth:'1px',borderColor:'white'}}
                          onClick={() => {this.Approve()}}>{this.state.symbol}</Button>
              </div>
              <div style={{color:"green",fontSize:"10px",textAlign:'center'}}>
                  {this.state.message1}
              </div>
              <div style={{marginTop:'5px'}}>
                    <Input  value={this.state.HBtotal} 
                      placeholder="" 
                      addonBefore='' 
                      suffix={
                        <Popover content={content12} trigger="click">
                          <QuestionCircleOutlined />  
                        </Popover>
                      }
                      onChange={event=>{this.setState({HBtotal:event.target.value})}}
                    />
              </div>

              <div >
                <Input addonBefore='' style={{marginTop:'3px'}}  value={this.state.HBamount} 
                  placeholder="" 
                  suffix={
                    <Popover content={content13} trigger="click">
                      <QuestionCircleOutlined />  
                    </Popover>
                  }
                  onChange={event=>{this.setState({HBamount:event.target.value})}}
                />
              </div>
              <div >
                    <Input addonBefore=''  value={this.state.HBremark} style={{marginTop:'3px'}} 
                      placeholder="" 
                      suffix={
                        <Popover content={content2} trigger="click">
                          <QuestionCircleOutlined />  
                        </Popover>
                      }
                      onChange={event=>{this.setState({HBremark:event.target.value})}}
                    />
              </div>

              <div style={{marginTop:'3px'}}>
                  <Input addonBefore=""  style={{ width: '50%'}} placeholder=">=,0" 
                    value={this.state.HBlownBalance}
                    suffix={
                      <Popover content={content4} trigger="click">
                        <QuestionCircleOutlined />  
                      </Popover>
                    }
                    onChange={event=>{this.setState({HBlownBalance:event.target.value})}} />
              </div>

              <div style={{marginTop:'5px'}}>
              <Input addonBefore="" style={{ width: '50%' }} placeholder=",0" 
                value={this.state.HBpassword}
                suffix={
                  <Popover content={content3} trigger="click">
                    <QuestionCircleOutlined />  
                  </Popover>
                }
                  onChange={event=>{this.setState({HBpassword:event.target.value})}} />
              </div>
            <Button  shape='round' block  size="large"
              style={{marginTop:'6px', background:'rgba(254, 73, 82,1)',color:this.state.disabled===''? 'white':'#ffffff4d',borderWidth:'2px',borderColor:'white'}} 
              disabled={this.state.disabled} 
              onClick={() => {this.giveHB()}}
              >
            </Button>
            <div style={{color:"green",fontSize:"13px",textAlign:'center'}}>
                      {this.state.message}
            </div>
          </Card >
          <Button style={{marginTop:'3px'}} type='dashed' shape='round' block size='small' onClick={this.open}></Button>
          <Card size='small' style={{borderRadius:'10px',display:this.state.open}}>
            <Input  value={this.state.HBid} placeholder="ID" 
              onChange={event=>{this.setState({HBid:event.target.value})}}/>
            <div style={{marginTop:'5px'}}>
              <Popover content={content6} trigger="click">
                    <QuestionCircleOutlined /> 
              </Popover>&nbsp;&nbsp;&nbsp;
              <Button style={{width:'100px'}} shape='round' onClick={() => {this.getHBbalance()}}  type="primary"  size='middle'></Button>
            </div>

            <div style={{marginTop:'5px'}} >
              <Popover content={content7} trigger="click">
                    <QuestionCircleOutlined /> 
              </Popover>&nbsp;&nbsp;&nbsp;
              <Button style={{width:'100px'}} shape='round' onClick={() => {this.getHBpassword()}}  type="primary"  size='middle'></Button>
            </div>

            <div style={{marginTop:'5px'}} >
              <Popover content={content9} trigger="click">
                    <QuestionCircleOutlined /> 
              </Popover>&nbsp;&nbsp;&nbsp;
              <Button style={{width:'100px'}} shape='round' onClick={() => {this.getHBlownBalance()}}  type="primary"  size='middle'></Button>
            </div>
            <div style={{color:"green",fontSize:"15px",textAlign:'center'}}>
                        {this.state.message2}
            </div>
            <div style={{marginTop:'5px'}}>
              <Popover content={content10} trigger="click">
                    <QuestionCircleOutlined /> 
              </Popover>&nbsp;&nbsp;&nbsp;
              <Button style={{width:'100px'}} shape='round' onClick={() => {this.modifyremark()}}  type="primary"  size='middle'></Button>
            </div>
            <Input  value={this.state.HBremark2} placeholder="" 
              onChange={event=>{this.setState({HBremark2:event.target.value})}}  />
        </Card>
        <Loading ref={loadwindow=>{this.loading=loadwindow}}/>
    </div>
  );
}
}

export default HBgive;
