import React, { Component } from 'react';
import '../../../App.css';
import web3 from "../../../ABI/web3";
import { Card, Button, Input, Popover, Divider } from 'antd';
import { QuestionCircleOutlined, CopyOutlined } from '@ant-design/icons';
import TokenFactoryABI from '../../../ABI/TokenFactory.json';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { getChainInfo, isApprove } from '../../../components/global/functions';
import Fun from "../../../components/global/function";
const BigNumber = require('bignumber.js');

class Hangqinadd extends Component {
  state = {
    name: '',
    symbol: '',
    decimals: '18',
    _totalSupply: '',
    tokenOwnerAddress: '',
    user: '',
    fee: '',
    message: '',
    newtokenAdr: '',
    GXvalue: '',
    SendDisabled: '',
    TokenFactoryAdr: '',
    feeLG: '',
    TokenFactoryContract: '',
    LGadr: '',
    LGApproveDisplay: 'none'
  };

  async componentDidMount() {
    this.first()
  }

  first = async () => {
    await this.getChain()
    let TokenFactory = await new web3.eth.Contract(TokenFactoryABI, this.state.TokenFactoryAdr)
    this.setState({
      tokenOwnerAddress: window.defaultAccount,
      user: window.defaultAccount,
      feeLG: '?',
      TokenFactoryContract: TokenFactory
    });
    let _fee = await TokenFactory.methods.Fee().call()
    let fee = await TokenFactory.methods.feeLG(_fee).call()
    fee = parseFloat(web3.utils.fromWei(await fee, "ether")).toFixed(1)
    this.setState({ fee });
    const newtokenAdr = await TokenFactory.methods.getLastERC20().call({ from: window.defaultAccount });
    if (newtokenAdr === '0x0000000000000000000000000000000000000000') {
      this.setState({ newtokenAdr: '' });
    } else {
      this.setState({ newtokenAdr });
    }
    this.isApproveLG()
  }

  getChain = async () => {
    await getChainInfo(window.ethereum.chainId).then((result) => {
      this.setState({
        TokenFactoryAdr: result.ChainAdr.TokenFactory,
        LGadr: result.ChainAdr.LG,
      })
    })
  }

  createERC20 = async () => {

    let TokenFactory = this.state.TokenFactoryContract
    const total = new BigNumber(this.state._totalSupply / 10 ** (18 - this.state.decimals)).toFixed();
    if (this.state._totalSupply <= 1000000000000000) {
      this.setState({ message: "" });
      await TokenFactory.methods.createERC20(
        this.state.name,
        this.state.symbol,
        this.state.decimals,
        web3.utils.toWei(total, 'ether'),
        this.state.tokenOwnerAddress,
      )
        .send({ from: window.defaultAccount });
      const newtokenAdr = await TokenFactory.methods.getLastERC20().call({ from: window.defaultAccount });
      this.setState({ newtokenAdr });
      this.setState({ message: "" });
    } else { this.setState({ message: "1000" }); }
  }

  isApproveLG = async () => {
    //LG
    await isApprove(this.state.LGadr, this.state.TokenFactoryAdr).then((result) => {
      if (result === 'no') {
        this.setState({ LGApproveDisplay: 'block', SendDisabled: 'disabled' })
      } else {
        this.setState({ LGApproveDisplay: 'none', SendDisabled: '' })
      }
    })
  }

  Approve = async (adr) => {
    await this.Fun.Approve(adr, this.state.TokenFactoryAdr)
    this.first()
  }

  render() {

    const content = (
      <div>
        <p></p>
      </div>
    );
    const content1 = (
      <div>
        <p>()bitcoin</p>
        <p></p>
      </div>
    );
    const content2 = (
      <div>
        <p>BTC</p>
        <p></p>
      </div>
    );
    const content3 = (
      <div>
        <p>18</p>
        <p></p>
      </div>
    );
    const content4 = (
      <div>
        <p>1000000000000000</p>
      </div>
    );
    const content5 = (
      <div>
        <p></p>
        <p></p>
      </div>
    );

    return (
      <div>
        <Card type="inner" size='small' title="ERC20" >
          <div >

            <Input id='i1' addonBefore="" value={this.state.user}
              suffix={
                <Popover content={content} trigger="click">
                  <QuestionCircleOutlined id='i1' />
                </Popover>
              }
              disabled />
          </div>

          <Divider></Divider>

          <div style={{ marginTop: '10px' }}>
            <Input addonBefore='' value={this.state.name}
              placeholder="bitcoin" size="large"
              suffix={
                <Popover content={content1} trigger="click">
                  <QuestionCircleOutlined />
                </Popover>
              }
              onChange={event => { this.setState({ name: event.target.value }) }} />
          </div>

          <div style={{ marginTop: '10px' }}>
            <Input addonBefore='' value={this.state.symbol}
              placeholder="BTC" size="large"
              suffix={
                <Popover content={content2} trigger="click">
                  <QuestionCircleOutlined />
                </Popover>
              }
              onChange={event => { this.setState({ symbol: event.target.value }) }} />
          </div>

          <div style={{ marginTop: '10px' }}>
            <Input addonBefore='' value={this.state._totalSupply}
              placeholder="21000000" size="large"
              suffix={
                <Popover content={content4} trigger="click">
                  <QuestionCircleOutlined />
                </Popover>
              }
              onChange={event => { this.setState({ _totalSupply: event.target.value }) }} />
          </div>

          <div style={{ marginTop: '10px' }}>
            <Input style={{ width: '50%' }} addonBefore='' value={this.state.decimals}
              placeholder=" 18" size="small"
              suffix={
                <Popover content={content3} trigger="click">
                  <QuestionCircleOutlined />
                </Popover>
              }
              onChange={event => { this.setState({ decimals: event.target.value }) }} />
          </div>

          <div style={{ marginTop: '10px' }}>
            <Popover content={content5} trigger="click">
              <QuestionCircleOutlined />
            </Popover>
          </div>
          <Input value={this.state.tokenOwnerAddress} placeholder=""
            onChange={event => { this.setState({ tokenOwnerAddress: event.target.value }) }} />

          <div style={{ display: this.state.LGApproveDisplay, marginTop: '10px' }}>
            <Button shape='round' block type="primary" size="middle"
              onClick={() => { this.Approve(this.state.LGadr) }}>LG</Button>
          </div>
          <Button shape='round' block type="primary" size="middle" style={{ marginTop: '10px' }}
            disabled={this.state.SendDisabled}
            onClick={() => { this.createERC20() }}></Button>

          <div style={{ color: "green", fontSize: "13px", textAlign: 'center' }}>
            {this.state.message}
          </div>

          <div >

            <div style={{ marginTop: '5px' }}>
              <Input id='i1' value={this.state.newtokenAdr} disabled placeholder=""
                suffix={
                  <CopyToClipboard text={this.state.newtokenAdr}>
                    <Button size="small" ><CopyOutlined /></Button>
                  </CopyToClipboard>
                }
              />
            </div>
          </div>
        </Card>
        <div style={{ marginLeft: '10px' }}>
          <div>{this.state.fee}LG</div>
          <div><QuestionCircleOutlined /><br />
            <br />
          </div>
          <div>
            <br />
            <br />
            
          </div>
        </div>
        <Fun ref={Fun => { this.Fun = Fun }} />
      </div>
    );
  }
}

export default Hangqinadd;
